import React, { Component } from 'react';

import classes from'./App.css';
import Person from './person/person'; 


class App extends Component{
  
 state={
  persons: [
    {name: 'srujana', age: 22},
    {name: 'Lokesh', age: 39},
    {name: 'Rajesh', age: 27}
            ],showPersons:true
        }
     togglePersonsHandler= ()=>{
   const doesShow=this.state.showPersons;
   this.setState({showPersons: !doesShow});
      }
 
nameChangedHandler = (event) =>{
this.setState({
persons:[
{name: 'Ranjit',age: 34},
{name: event.target.value, age: 39},
{name: 'Rajesh', age: 27}
       ]
                    
});
                    }
                    deletePersonHandler=(personIndex) =>{
                      const newPersons=this.state.persons;
                      newPersons.splice(personIndex,1);
                      this.setState({Persons:newPersons});
                     }
     render(){
      
   let persons=null;
   let btnClass=[classes.Button];

if(this.state.showPersons){
  persons=(
	<div>
		{this.state.persons.map((person,index) =>{

return <Person click={this.deletePersonHandler.bind(this,index)} name={person.name} age={person.age} />

})}
</div>
);
 
btnClass.push(classes.Green);    

}


const myclasses=[];

if(this.state.persons.length<=2)
{
myclasses.push('one');
}

if(this.state.persons.length<=1)
{
myclasses.push('two');
}

return (
  
<div className={classes.App}>
<h1>HELLO</h1>
<p className={myclasses.join(' ')}>HOW ARE YOU </p>
<button className={btnClass.join(' ')}  onClick={this.togglePersonsHandler}>Toggle persons</button>
{persons}
   </div>
   
       );
 };
}
    


  
   export default App;

  // const persons=this.state.persons.slice();
   //(or)
   //const persons=[...this.state.persons]; // ... spread operator
   
   


